// import DragResize from './components/DragResize.vue'
// import Notification from './Notification.vue'
// import { createNamespacedHelpers } from 'vuex'

// const { mapGetters, mapActions } = createNamespacedHelpers('Notification')
export default {
  name: 'NotificationModal',
  components: { Notification, DragResize },
  data() {
    return {
      coordinates: {
        width: Quasar.plugins.Screen.width - 100,
        height: Quasar.plugins.Screen.height - 100,
        top: 0,
        left: Quasar.plugins.Screen.width - (Quasar.plugins.Screen.width - 100 / 2)
      }
    }
  },
  computed: {
    ...mapGetters(['maximized', 'moduleName']),
    dialog: {
      set(val) {
        this.$store.commit('Notification/setDialog', val)
      },
      get() {
        return this.$store.getters['Notification/dialog']
      }
    },
    maximized: {
      set(val) {
        this.$store.commit('Notification/setMaximized', val)
      },
      get() {
        return this.$store.getters['Notification/maximized']
      }
    }
  },
  methods: {
    ...mapActions(['fetchCategories', 'initState']),
    onChangeCoordinates: function(coordinates) {
      this.coordinates = coordinates
    }
  },
  async created() {
    await this.initState(this.$options.state)
  }
}
